@use "../../assets/scss/" as *;

.library__container {
  @include flex(null, null);
  height: 100%;
  flex: 100;
  flex-direction: column;

  .library__title {
    padding: 15px 20px;
    font-size: 16px;
    font-weight: 700;
  }

  .library__playlists {
    height: 100%;
    flex: 80;
    overflow-y: scroll;
    overflow-x: hidden;

    .library__playlist--item {
      @include flex(center, null);
      padding: 10px 20px;
      transition: all 0.3s ease;
      cursor: pointer;

      .library__playlist--img {
        border-radius: 50%;
        width: 15%;
        margin-right: 20px;
      }

      &:hover {
        background-color: $c-black;
        color: $c-white;
        transform: scale(1.005);
      }
      &.active {
        color: $c-white;
        font-weight: 700;
      }
    }

    &::-webkit-scrollbar {
      width: 7px;
    }

    &::-webkit-scrollbar-track {
      background: $c-black;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $c-grey;
    }
    & {
      scrollbar-width: auto;
      scrollbar-color: $c-grey $c-black;
    }
  }
}
