@use "../../assets/scss/" as *;

.sidebar__menu {
  @include flex(center, null);
  flex-direction: row;
  transition: all 0.3s ease;

  .btn__title {
    margin-left: 30px;
  }

  &:hover {
    color: $c-white;
    transform: scale(1.005);
  }

  &.active {
    color: $c-white;
    font-weight: 700;
  }
}
