@use "../../assets/scss/" as *;

.playlist__container {
  height: 100%;
  overflow: hidden;
  .playlist__container--row {
    @include flex(center, null);
    flex-direction: row;
    margin: 16px;
    padding: 16px 30px;
    border-bottom: 1px solid $bg-black;
  }

  .col-id {
    flex: 1;
  }

  .col-title {
    flex: 5;
    @include flex(center, null);

    .song__img {
      border-radius: 50%;
      width: 10%;
      margin-right: 20px;
    }

    .song__title {
      @include flex(null, null);
      flex-direction: column;
      .song__name {
        font-size: 1.1rem;
      }

      .song__artist {
        margin-right: 10px;
      }
    }
  }

  .col-album {
    flex: 3;
  }

  .col-time {
    flex: 1;
  }

  .playlist__song {
    height: 100%;
    overflow: scroll;
    height: inherit;
    color: $c-grey;

    .playlist__container--tracks {
      cursor: pointer;
      @include flex(center, null);
      flex-direction: row;
      margin: 0 16px;
      padding: 16px 30px;
      transition: all 0.3s ease;
      &:hover {
        background-color: $bg-black;
        color: $c-white;
        border-radius: 7px;
      }

      &.active {
        background-color: $bg-black;
        color: $c-white;
        border-radius: 7px;
      }
    }

    &::-webkit-scrollbar {
      width: 7px;
    }

    &::-webkit-scrollbar-track {
      background: $c-black;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $c-grey;
    }
    & {
      scrollbar-width: auto;
      scrollbar-color: $c-grey $c-black;
    }
  }
}
