@use "../../assets/scss/" as *;

.section__container {
  background-color: $c-black;
  padding: 10px 0;
  position: relative;
  top: 7vh;
  height: 85vh;
  overflow: scroll;

  &::-webkit-scrollbar {
    width: 7px;
  }

  &::-webkit-scrollbar-track {
    background: $c-black;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $c-grey;
  }
  & {
    scrollbar-width: auto;
    scrollbar-color: $c-grey $c-black;
  }
}
