@use "../../assets/scss/" as *;

.sidebar__wrapper {
  @include flex(null, center);
  flex-direction: column;
  height: 100%;
  flex: 100;
  background-color: $bg-black;
  color: $c-grey;
  font-weight: 600;

  .sidebar__header {
    padding: 20px 20px 0 20px;
    @include flex(null, space-evenly);
    flex-direction: column;
    height: 15%;
    border-bottom: 2px solid $c-black;
  }
  .sidebar__section {
    height: calc(100% - 15%);
  }
}
