@use "../../assets/scss/" as *;
.home__recommeneded {
  display: flex;
  flex-direction: column;
  padding: 20px;
  height: 40vh;
  width: 100%;

  .recommended__title {
    color: $c-grey;
    font-size: 16px;
    font-weight: 600;
    padding: 10px;
    @include flex(center, space-between);

    h2 {
      cursor: pointer;
      transition: all 0.3s ease;
    }

    p {
      @extend h2;
    }

    h2:hover {
      color: $c-white;
      text-decoration: underline;
    }
  }
  .recommeneded__container {
    @include flex(null, space-around);
    flex-direction: row;
    flex-wrap: wrap;
    text-overflow: ellipsis;
    width: 100%;
    height: 100%;
  }
}
