@use "../../assets/scss/" as *;

.footerAudioControl {
  color: $c-grey;
  width: 50vw;
  .audio__control {
    @include flex(center, center);

    .audio__control--item {
      cursor: pointer;
      font-size: 25px;
      margin: 0 25px;
      @include flex(center, center);
      transition: all 0.3s ease;

      &:hover {
        color: $c-white;
        transform: scale(1.05);
      }
    }
  }
}
