@use "../../assets/scss/" as *;

.playlists__container {
  color: $c-white;
  height: 100%;
  @include flex(null, null);
  flex-direction: column;
  .playlist__header {
    overflow: hidden;
    padding: 0 50px;
    padding-bottom: 20px;
    height: 25vh;
    @include flex(center, null);
    border-bottom: 1px solid $bg-black;
    .playlist__header--img {
      height: 100%;
      border-radius: 50%;
    }

    .playlist__header--content {
      margin-left: 30px;
      .playlist__header--name {
        font-size: 3vw;
      }
    }
  }
}
