@use "../../assets/scss/" as *;

.recommeneded__item {
  cursor: pointer;
  border-radius: 5px;
  padding: 10px;
  width: 10vw;
  height: 100%;
  background-color: $bg-black;
  color: $c-grey;

  &:hover {
    color: $c-white;
    background-color: $c-grey;
  }

  @include flex(center, space-between);
  flex-direction: column;

  .recommeneded__item--img {
    width: 100%;
    padding: 15px;
    border-radius: 20px;
  }

  .recommeneded__item--title {
    @include flex(null, null);
    flex-direction: column;
    overflow: hidden;
    h4 {
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
