@use "../../assets/scss/" as *;

.home__container {
  height: 100%;
  overflow: scroll;

  &::-webkit-scrollbar {
    width: 7px;
  }

  &::-webkit-scrollbar-track {
    background: $c-black;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $c-grey;
  }
  & {
    scrollbar-width: auto;
    scrollbar-color: $c-grey $c-black;
  }
}
