@use "../../assets/scss/" as *;

.page__login {
  background-color: $c-black;
  height: 100vh;
  width: 100vw;
  @include flex(center, center);
  overflow: hidden;
  flex-direction: column;

  .login--logo {
    width: 40%;
  }

  .btn__login {
    width: 200px;
    padding: 15px 0px;
    text-align: center;
    background-color: $c-grey;
    border-radius: 99999px;
    color: $c-white;
    font-weight: 600;
    margin-top: 20%;
  }
}
