@forward "./variables";
@forward "./mixin";
@forward "./breakpoint";

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;

  .header {
    height: 92vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    .sidebar {
      width: 25vw;
    }
    .section {
      width: inherit;
      height: 92vh;
      position: relative;
    }
  }
  .footer {
    border-top: 1px solid #181818;
    height: 8vh;
    background-color: #121212;
  }
}

@media screen and (max-width: 600px) {
  .sidebar {
    display: none;
  }
}

@media screen and (max-width: 1024px) {
  .sidebar {
    display: none;
  }
}
